import {Ref} from "vue";
import {Map} from "mapbox-gl";

/**
 * 改变交通信息的可见状态。它会直接在地图中改变可变性而不是重新加载/去除layer减少加载时的资源消耗。
 * @param map
 * @param layers 记录地图中交通layer的数组
 */
export function changeTrafficMapVisibility(map: Ref<Map>, layers: Ref<string[]>) {
    for (let layer of layers.value) {
        const visibility = map.value.getLayoutProperty(
            layer,
            'visibility'
        );

        if (visibility === 'visible') {
            map.value.setLayoutProperty(layer, 'visibility', 'none');

        } else {
            map.value.setLayoutProperty(
                layer,
                'visibility',
                'visible'
            );
        }
    }

}

